.map {
    height: 500px;
    background-color: white;
    padding: 1rem;
    border-radius: 20px;
    margin-top: 20px;
    box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
    margin-bottom: 30px;
}

.map .leaflet-container {
    height: 100%;
    border-radius: 4px;
}

.info-flag {
    height: 100px;
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 8px;
    margin-bottom: 20px;
}

.info-flag img {
    width: 100px;
    border-radius: 5px;
}

.info-name {
    font-size: 20px;
    font-weight: bold;

}

.info-container {
    width: 150px;
}

.info-confirmed,.info-recovered, .info-deaths {
    font-size: 14px;
    margin-top: 5px;
}




