* {
  margin: 0;
}

body {
  background-color: #f5f6fa;
}

h3 {
  font-size: 24px;
}

.app {
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
}

.app__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;

}

.app__header > h1 {
  color: #fc3c3c;
  font-size: 2rem;
}

.app__dropdown {
  background-color: white;
}

.graph__title {
  margin-top: 20px;
}


.app__stats {
  display: flex;
  justify-content: space-between;
}

.app__left {
  flex: 0.9;
}

.app__right {
  width: 500px;
}


@media (max-width:990px) {
  .app {
    flex-direction: column;
  }
  .app__right {
    width: auto;
  }
}