.table {
    margin-top: 20px;
    overflow-y: scroll;
    height: 400px;
    color: #6a5d5d;
}

.table tr {
    display: flex;
    justify-content: space-between;
}


.table tr:nth-of-type(odd) {
    background-color: #f3f2f8;
}


::-webkit-scrollbar {
    display: none;
}

.line_graph {
    height: 600px;
    padding-top: 30px;
    padding-bottom: 30px;
}